$gray: #aeaeae;
$grayLight: #babcbb;
$grayDark: #767676;
$red: #cc0017;
$redLight: #ee0017;
$hoverBtn: #f8f8f8;
@charset "UTF-8";
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  font-family: custom-regular;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

h1, h2, h3 {
  font-family: custom-regular;
}

section {
  position: relative;
}

img {
  width: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  -o-object-position: center;
  object-position: center;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

@font-face {
  font-family: 'custom-regular';
  src: url("../fonts/Mark-Simonson/regular/ProximaNova-Regular.eot") format("eot"),
  url("../fonts/Mark-Simonson/regular/ProximaNova-Regular.woff") format("woff"),
  url("../fonts/Mark-Simonson/regular/ProximaNova-Regular.ttf") format("truetype"),
  url("../fonts/Mark-Simonson/regular/ProximaNova-Regular.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'custom-bold';
  src: url("../fonts/Mark-Simonson/bold/ProximaNova-Bold.eot") format("eot"),
  url("../fonts/Mark-Simonson/bold/ProximaNova-Bold.woff") format("woff"),
  url("../fonts/Mark-Simonson/bold/ProximaNova-Bold.ttf") format("truetype"),
  url("../fonts/Mark-Simonson/bold/ProximaNova-Bold.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'custom-black';
  src: url("../fonts/Mark-Simonson/black/ProximaNova-Black.eot") format("eot"),
  url("../fonts/Mark-Simonson/black/ProximaNova-Black.woff") format("woff"),
  url("../fonts/Mark-Simonson/black/ProximaNova-Black.ttf") format("truetype"),
  url("../fonts/Mark-Simonson/black/ProximaNova-Black.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

p, span {
  font-family: custom-regular;
}

button, input, a {
  cursor: pointer;
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

a {
  -webkit-tap-highlight-color: transparent;
  text-decoration: none;
  font-family: custom-regular;

}

ul {
  list-style: none;
}

a, button {
  transition: .3s;
  &:hover {
    transition: .3s;

  }
}

section {
  margin: auto;
}

//common block start
.sort__method {
  position: relative;
  font-size: 18px;
  display: flex;
  align-items: flex-end;
  margin-bottom: -5px;
  padding-right: 20px;
  padding-left: 15px;
  .method__list {
    a {
      white-space: nowrap;
      padding: 2px 5px;
      font-size: 16px;
      color: #babcbb;
      &:hover {
        background: #aeaeae;
        color: black;
      }
    }
    border: 1px solid #c7c7c7;
    z-index: 3;
    box-shadow: 10px 10px 35px -12px rgba(0, 0, 0, 0.75);
    box-sizing: border-box;
    top: 45px;
    right: 4px;
    background: white;
    padding: 5px;
    position: absolute;
    visibility: hidden;
    display: flex;
    flex-direction: column;
    transition: .2s;
    max-height: 0px;
    overflow: hidden;
    &.active {
      transition: .2s;
      max-height: 180px;
      visibility: visible;
      overflow: unset;
    }
  }
  .triangle-left {
    position: absolute;
    top: -10px;
    right: -1px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0 10px 7.5px;
    border-color: transparent transparent #c7c7c7 transparent;
  }
  .triangle-left:after {
    position: absolute;
    content: "";
    display: block;
    top: 3px;
    z-index: 3;
    left: -6.5px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0 8px 5.5px;
    border-color: transparent transparent white transparent;
    border-color: transparent transparent white transparent;
  }
  .selected__method {
    cursor: pointer;
    &:hover {
      span {
        text-decoration: underline;
      }
    }
  }
  span {
    white-space: nowrap;
    position: relative;
    &:after {
      position: absolute;
      top: 9px;
      right: -19px;
      width: 10px;
      height: 11px;
      content: "";
      background: url(../images/icons/triangle.png) no-repeat;
      background-size: contain;
    }
  }
}

.hidden-object {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.breadcrumb {
  .container-block {
    width: 100%;
  }
  width: 100%;
  display: flex;
  align-items: flex-start;
  font-size: 16px;
  margin-bottom: 35px;
  a, span {
    white-space: nowrap;
    color: $grayLight;
  }
  ul {
    width: 100%;
    display: flex;
    align-items: center;
  }
  .slash {
    margin: 0 5px;
    color: #b2b4b3;
  }
}
@media screen and (max-width:480px){
  .breadcrumb ul{
    overflow-x: scroll;
  }
}
.icon-close {
  width: 27px;
  height: 27px;

  span:before, span:after {
    top: 11px;
    left: 6px;
    width: 13px;
  }
  span:before {
    transform: rotate(45deg);
  }
  span:after {
    transform: rotate(-45deg);
  }
  span,
  span:before,
  span:after {
    cursor: pointer;
    border-radius: 1px;
    position: absolute;
    display: block;
    content: '';
    transition: all 300ms ease-in-out;
    width: 13px;
    background: $red;
    height: 3px;
  }
  span {
    background: transparent;
    width: 25px;
    height: 25px;
    -webkit-tap-highlight-color: transparent;
    border-radius: 50%;
    border: 1px solid #aeaeae;
  }
}

.slick-prev {
  z-index: 2;
  left: 10px !important;
  width: 65px !important;
  height: 65px !important;
}

.slick-dots li button {
  padding: 0 !important;
  width: 16px !important;
  height: 16px !important;
}

.slick-dots li {
  height: 16px !important;
  width: 16px !important;
}

.slick-dots li button:before {
  content: "" !important;
  width: 14px !important;
  opacity: 1 !important;
  height: 14px !important;
  background: $grayDark;
  border-radius: 50%;
  font-size: 18px !important;
}

.slick-dots li.slick-active button:before {
  background: white;
  border: 1px solid $grayDark;

}

.slick-next {
  width: 65px !important;
  height: 65px !important;
  z-index: 2;
  right: 10px !important;
}

.slick-next:before {
  width: 65px;
  height: 65px;
  display: block;
  background: url("../images/icons/arrow_right_red.svg") no-repeat;
  background-size: contain;
  content: "" !important;
  font-family: custom-regular !important;
}

.slick-prev:before {
  width: 65px;
  height: 65px;
  display: block;
  font-family: custom-regular !important;
  content: "" !important;
  background: url("../images/icons/arrow_right_red.svg") no-repeat;
  background-size: contain;
  transform: rotateZ(180deg);
}

.white-arrows {
  .slick-prev {
    left: 10px !important;
  }
  .slick-next {
    right: 10px !important;
  }
  .slick-next:before {
    background: url("../images/icons/arrow_right.svg") no-repeat;
    background-size: contain;
  }
  .slick-prev:before {
    background: url("../images/icons/arrow_right.svg") no-repeat;
    background-size: contain;
    transform: rotateZ(180deg);
  }
}

@media screen and (max-width: 576px) {
  .white-arrows {
    .slick-prev {
      width: 24px !important;
      height: 7px !important;
    }
    .slick-next {
      width: 24px !important;
      height: 7px !important;
    }
    .slick-next, .slick-prev {
      bottom: -17px;
      transform: unset;
      top: unset;
    }
    .slick-prev:before {
      width: 24px !important;
      height: 7px !important;
      background: url("../images/icons/arow_r_icon.svg") no-repeat;
      background-size: contain;
    }
    .slick-next:before {
      width: 24px !important;
      height: 7px !important;
      background: url("../images/icons/arow_r_icon.svg") no-repeat;
      background-size: contain;
    }
  }
  .slick-prev {
    width: 24px !important;
    height: 7px !important;
  }
  .slick-next {
    width: 24px !important;
    height: 7px !important;
  }
  .slick-next, .slick-prev {
    bottom: -17px;
    transform: unset;
    top: unset;
  }
  .slick-prev:before {
    width: 24px !important;
    height: 7px !important;
    background: url("../images/icons/arow_r_icon.svg") no-repeat;
    background-size: contain;
  }
  .slick-next:before {
    width: 24px !important;
    height: 7px !important;
    background: url("../images/icons/arow_r_icon.svg") no-repeat;
    background-size: contain;
  }
}

.custom-button {
  border: none;
  text-transform: uppercase;
  font-size: 25px;
  color: white;
  background: $red;
  padding: 12px 25px;
  &:hover {
    text-decoration: underline;
    box-shadow: 0px 0px 13px -2px black;
  }
}

.custom-ul {
  padding-left: 25px;
  list-style: none;
  display: flex;
  flex-direction: column;
  li {
    position: relative;
    margin-bottom: 16px;
    &:before {
      left: -25px;
      position: absolute;
      content: "";
      top: 3px;
      background: url("../images/bulet.svg") no-repeat;
      background-size: contain;
      height: 14px;
      width: 14px;
      display: block;

    }
  }
  a {
    font-size: 17px;
    color: $gray;
  }
}

.container-block {
  max-width: 1750px;
  margin: auto;
  padding: 0 25px;
  box-sizing: border-box;
}

@media screen and (max-width: 480px) {
  .container-block {
    padding: 0 15px;
  }
}

.title-line-block {
  display: flex;
  margin-bottom: 40px;
  justify-content: flex-start;
  .title-line-block-text {
    display: flex;
    align-items: flex-end;
    .title-line-block-text-text {
      white-space: nowrap;
      text-transform: uppercase;
      font-size: 33px;
      color: black;
    }
    margin-bottom: -10px;
    margin-right: 25px;
  }
  .title-line-block-line {
    width: 100%;
    border-bottom: 2px solid $gray;
  }
  .title-line-block-link {
    margin-left: 25px;
    display: flex;
    align-items: flex-end;
    text-transform: lowercase;
    color: $gray;
    text-decoration: underline;
    font-size: 29px;
    &:hover {
      color: black;
    }
  }
}

@media screen and(max-width: 1280px) {
  .title-line-block .title-line-block-text .title-line-block-text-text {
    font-size: 25px;
  }
  .title-line-block .title-line-block-link {

    font-size: 23px;
  }
}

@media screen and(max-width: 576px) {
  .title-line-block .title-line-block-line {
    display: none;
  }
  .title-line-block .title-line-block-link {
    display: none;
  }
}

.frame-button-r-block {
  padding: 25px 25px 65px 25px;
  box-sizing: border-box;
  border: 1px solid #aeaeae;
  position: relative;
  .link {
    padding: 30px 0 0 30px;
    a {
      text-transform: uppercase;
      padding: 12px 20px;
      border: 1px solid #a77872;
      display: flex;
      align-items: center;
      justify-content: center;
      color: $gray;
      font-size: 21px;
      transition: .3s;

      &:hover {
        border-color: #ab928d;
        box-shadow: 0px 0px 5px -2px #000000;
        transition: 0s;
        color: black;
        //background: $hoverBtn;
        text-decoration: underline;
      }
      margin-right: 1px;
      margin-bottom: 1px;
    }
    box-sizing: border-box;
    position: absolute;
    display: flex;
    background: white;
    align-items: flex-end;
    justify-content: flex-end;
    bottom: -2px;
    right: -2px;
  }
}

@media screen and (max-width: 1450px) {
  .frame-button-r-block .link a {
    padding: 7px 12px;
    font-size: 17px;
  }
  .frame-button-r-block {
    padding: 15px 15px 45px 15px;
  }
