//header start
.header {
  .language {
    position: relative;
  }
  .triangle-left {
    position: absolute;
    top: -10px;
    left: -1px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 7.5px 10px 0px;
    border-color: transparent transparent #c7c7c7 transparent;
    &:after {
      position: absolute;
      content: "";
      display: block;
      top: 3px;
      z-index: 3;
      left: 1px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0px 5.5px 7px 0px;
      border-color: transparent transparent white transparent;
    }
  }
  .language-list {
    border: 1px solid #c7c7c7;
    z-index: 3;
    box-shadow: 10px 10px 35px -12px rgba(0, 0, 0, 0.75);
    box-sizing: border-box;
    top: 36px;
    left: 10px;
    background: white;
    padding: 5px;
    position: absolute;
    visibility: hidden;
    display: flex;
    flex-direction: column;
    transition: .2s;
    max-height: 0px;
    overflow: hidden;
    a {
      padding: 2px 15px;
      font-size: 14px;
      color: $grayLight;
      &:hover {
        background: $gray;
        color: black;
      }
    }
    &.active {
      transition: .2s;
      max-height: 80px;
      visibility: visible;
      overflow: unset;
    }
  }
  &.fixed {
    position: fixed;
    width: 100%;
    z-index: 34;
    background: white;
    top: 0;
    & + section {
      margin-top: 160px;
    }
  }
  .language {
    font-size: 21px;
  }
  .current-language {
    padding-right: 30px;
    &:hover {
      span {
        text-decoration: underline;
      }
    }
    cursor: pointer;
    display: flex;
    span {
      position: relative;
      &:after {
        position: absolute;
        top: 6px;
        right: -19px;
        width: 10px;
        height: 11px;
        content: "";
        background: url("../images/icons/triangle.png") no-repeat;
        background-size: contain;
      }
    }
    span, a {
      transition: .3s;
      color: black;
      font-size: 14px;
      &:hover {
        transition: .3s;

        //color: $gray;
      }
    }
    img {
      margin-right: 10px;
      width: 19px;
      height: 19px;
      margin-top: 3px;
    }
  }
  .header-top-line {
    margin-bottom: 6px;
    border-bottom: 1px solid $gray;
    .container-block {
      width: 100%;
      height: 40px;
      align-items: center;
      display: flex;
      justify-content: space-between;
    }
  }
  .auth {
    display: flex;
    img {
      width: 14px;
    }
  }
  .auth-block {
    font-size: 14px;
    margin-left: 10px;
    align-items: center;
    display: flex;
    width: 126px;
    justify-content: space-between;
    a {
      color: black;
      &:first-child {
        position: relative;
        &:after {
          position: absolute;
          top: 0px;
          right: -16px;
          display: block;
          height: 100%;
          width: 1px;
          background: $gray;
          content: "";
        }
      }
      &:hover {
        text-decoration: underline;
      }
      //&:hover{
      //  position: relative;
      //  &:before{
      //    content: "";
      //    display: block;
      //    width: 100%;
      //    height: 1px;
      //    position: absolute;
      //    left: 0;
      //    bottom: 3px;
      //    background: $grayDark;
      //  }
      //}
    }
  }
  .header-bottom-line {
    position: relative;
    .container-block {
      height: 118px;
      align-items: center;
      display: flex;
      justify-content: space-between;
      width: 100%;
    }

  }
  .fav {
    margin-left: 20px;
    width: 32px;
    height: 28px;
    display: block;
    .fav-img {
      width: 100%;
      height: 100%;
      display: block;
      background: url("../images/icons/heart.svg") no-repeat;
      background-size: contain;
      position: relative;
      &:hover {
        background: url(../images/icons/heart-red.svg) no-repeat center;
        background-size: contain;
      }
    }
  }
  .tools {
    display: flex;
  }
  .search {
    position: relative;
    height: 31px;
    width: 53px;
    display: flex;
    .search-in {
      margin-left: 20px;

      transition: .3s;
      width: 33px;
      overflow: hidden;

    }
    input {
      height: 30px;
      padding: 0;
      border: none;
      display: none;
      box-sizing: border-box;
      width: 0;
      overflow: hidden;
    }
    &:hover {

      height: 31px;
      .search-in {
        margin-left: 0px;

        transition: .3s;
        width: 143px;
        overflow: unset;

        display: flex;
        right: 0px;
        position: absolute;
      }
      input {
        display: block;
        transition: .3s;
        width: 110px;
        padding: 5px 5px 5px 15px;
        height: 30px;
        border: 1px solid $grayDark;
      }
      .search-btn {
        transition: 0s;
        background: url("../images/icons/zoom-white.svg") no-repeat center;
        background-size: 20px;
        background-color: $grayDark;

        margin-left: 0px;
      }
    }
  }
  .search-btn {
    cursor: pointer;
    display: block;
    height: 30px;
    transition: 0s;

    width: 33px;
    background: url("../images/icons/zoom.svg") no-repeat;
    background-size: contain;
  }
  .bucket-img {
    display: block;
    height: 30px;
    width: 33px;
    background: url("../images/icons/basket.svg") no-repeat;
    background-size: contain;
    position: relative;
    &:hover {
      background: url("../images/icons/basket-red.svg") no-repeat;
      background-size: contain;
    }
    .bucket-amount {
      height: 30px;
      background: white;
      width: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      bottom: -12px;
      left: -7px;
      position: absolute;
      span {
        border: 1px solid $red;
        background: white;
        height: 20px;
        width: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        font-size: 11px;
        color: black;
      }
    }
  }
}

