.menu-mob span:before {
  top: -10px;
}

.menu-mob span:after {
  bottom: -10px;
}

.menu-mob.act span {
  background: transparent;
}

.menu-mob.act span:before {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.menu-mob.act span:after {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

