        font-size: 19px;
        position: relative;
        padding-left: 25px;
        margin-bottom: 10px;
        display: flex;
        align-items: center;
        input {
          margin-right: 10px;
        }
      }
    }
    .price-slider {
      display: flex;
      justify-content: space-between;
    }
    .slide input[type=text] {
      width: calc(50% - 40px);
      padding: 2px 5px;
      height: 26px;
    }
    .custom-link {
      margin: 25px auto;
    }
    .f-title {
      -webkit-tap-highlight-color: transparent;
      cursor: pointer;
      padding: 0px 25px 0px 0;
      font-size: 23px;
      position: relative;
      &:after {
        background: url(../images/icons/triangle.png) no-repeat;
        background-size: contain;
        content: "";
        transition: .3s;
        width: 15px;
        height: 11px;
        position: absolute;
        display: block;
        color: black;
        top: 9px;
        right: 0px;
        transform: rotateZ(180deg);
      }
      &.active:after {
        transform: unset;
        top: 12px;

      }
    }
    .item-in {
      padding: 0px;
    }
    border-bottom: 1px solid white;
    width: calc(100% - 50px);
    margin: auto;
    padding: 12px 0px 12px;
    box-sizing: border-box;
    overflow: hidden;
    transition: all .4s ease-out;
  }
}

